import axios from 'axios';
import React, { useEffect, useState } from 'react'
import MediaItem from '../MediaItem/MediaItem';
import { Helmet } from 'react-helmet';
import LoadingScreen from '../../LoadingScreen/LoadingScreen';



export default function Home() {
    let [trendingMovies, setTrendingMovies] = useState([]);
    let [trendingPerson, setTrendingPerson] = useState([]);
    let [trendingTv, setTrendingTv] = useState([]);
    let [loadingScreen, setLoadingScreen] = useState(true);

    async function getTrending(mediaType, callback) {
        setLoadingScreen(true);

        let { data } = await axios.get(`https://api.themoviedb.org/3/trending/${mediaType}/week?api_key=025437452774b9a20481138d2e38affc`);
        // console.log(data.results)
        callback(data.results)
        setLoadingScreen(false);
    }

    useEffect(() => {
        getTrending('movie', setTrendingMovies);
        getTrending('person', setTrendingPerson);
        getTrending('tv', setTrendingTv);
    }, [])

    return (
        <>
            <Helmet>
                <title>Home</title>
                <meta name="description" content="home page" />
            </Helmet>
            {/* movies */}
            {loadingScreen ? <LoadingScreen /> :
                <>

                    <div className="row">
                        <div className="col-md-4 ">
                            <div className="brdr w-25 my-3"></div>
                            <h2 >Trending movies <br />
                                to watch now</h2>
                            <h3 className='h6'>most popular movies </h3>
                            <div className="brdr w-75 my-3"></div>
                        </div>

                        {trendingMovies.filter((movie) => movie.poster_path !== null).map((item, index) => <MediaItem key={index} item={item} mediaType={item.media_type || 'movie'} />)}

                    </div>
                    {/* TV */}
                    <div className="row">
                        <div className="col-md-4 my-3">
                            <div className="brdr w-25"></div>
                            <h2 >Trending TV <br />
                                to watch now</h2>
                            <h3 className='h6'>most popular TV </h3>
                            <div className="brdr w-75 my-3"></div>
                        </div>
                        {trendingTv.filter((tv) => tv.profile_path !== null).map((item, index) => <MediaItem key={index} item={item} mediaType={item.media_type || 'tv'} />)}

                    </div>
                    {/* People */}

                    <div className="row">
                        <div className="col-md-4 ">
                            <div className="brdr w-25 my-3"></div>
                            <h2 >Trending People <br />
                                to watch now</h2>
                            <h3 className='h6'>most popular People </h3>
                            <div className="brdr w-75 my-3"></div>
                        </div>
                        {trendingPerson.filter((person) => person.profile_path !== null).map((item, index) => <MediaItem key={index} item={item} mediaType={item.media_type || 'person'} />)}

                    </div>
                </>
            }

        </>
    )
}
