import React from 'react';
import { Link } from 'react-router-dom';

export default function MediaItem({ item, mediaType }) {
    return (
        <div className="col-md-2 position-relative overflow-hidden mb-3">
            <Link to={`/itemdetails/${item.media_type || mediaType}/${item.id}`} className='linkStyle link-hover position-relative'>
                <div className="item position-relative overflow-hidden">
                    {item.poster_path ? <img className='w-100' src={"https://image.tmdb.org/t/p/w500" + item.poster_path} alt="" /> : <img className='w-100' src={"https://image.tmdb.org/t/p/w500" + item.profile_path} alt="" />}
                    {item.vote_average && item.vote_average !== '0' ? <div className="vote position-absolute top-0 end-0 p-2">{item.vote_average.toFixed(1)}</div> : ''}
                </div>
                {/* Move overlay-item inside the Link component */}
                <div className="overlay-item position-absolute d-flex justify-content-center align-items-center p-3">
                    {/* Ensure that item.title or item.name is not null */}
                    <h3 className='h6'>{item.title || item.name}</h3>
                </div>
            </Link>
        </div>
    );
}
