// PersonMovies.js
import axios from 'axios';
import React, { useEffect, useState } from 'react';
import MediaItem from '../MediaItem/MediaItem';
import LoadingScreen from '../../LoadingScreen/LoadingScreen';

export default function PersonMovies({ id }) {
    let [personMovies, setPersonMovies] = useState([]);
    let [loadingScreen, setLoadingScreen] = useState(true);


    async function fetchPersonMovies() {

        setLoadingScreen(true)


        let { data } = await axios.get(`https://api.themoviedb.org/3/person/${id}/movie_credits?api_key=025437452774b9a20481138d2e38affc`);
        setPersonMovies(data.cast);
        setLoadingScreen(false)

    }

    useEffect(() => {
        fetchPersonMovies();
    }, []);

    return (
        <>
            {loadingScreen ? <LoadingScreen /> :
                <>
                    <div className="row">
                        {personMovies.filter((movie) => movie.poster_path).map((movie) => <MediaItem item={movie} key={movie.id} mediaType={'movie'} />)}
                    </div>


                </>
            }

        </>
    );


}
