import React, { useEffect, useState } from 'react'
import { RouterProvider, createBrowserRouter } from 'react-router-dom'
import Layout from '../Layout/Layout'
import Home from '../Home/Home';
import Movies from '../Movies/Movies';
import People from '../People/People';
import TvShow from '../TvShow/TvShow';
import Profile from '../Profile/Profile'
import Login from '../Login/Login';
import Register from '../Register/Register';
import { jwtDecode } from 'jwt-decode';
import ProtectedRoute from '../ProtectedRoute/ProtectedRoute';
import ItemDetails from '../ItemDetails/ItemDetails';
import UpComing from '../UpComing/UpComing';
import { Offline } from 'react-detect-offline';

export default function App() {
  // Handling reload
  useEffect(() => {
    if (localStorage.getItem('token') !== null) {
      saveUserData();
    }
  }, [])

  // Routing
  let [userData, setUserData] = useState(null);


  function saveUserData() {
    let encoded = localStorage.getItem('token');
    let decoded = jwtDecode(encoded);
    console.log(decoded)
    setUserData(decoded);
  }

  let routers = createBrowserRouter([
    {
      path: '/', element: <Layout userData={userData} setUserData={setUserData} />, children: [
        { index: true, element: <ProtectedRoute><Home /></ProtectedRoute> },
        { path: 'movies', element: <ProtectedRoute><Movies /> </ProtectedRoute> },
        { path: 'people', element: <ProtectedRoute><People /></ProtectedRoute> },
        { path: 'itemdetails/:media_type/:id', element: <ProtectedRoute><ItemDetails /></ProtectedRoute> },
        { path: 'tv', element: <ProtectedRoute><TvShow /></ProtectedRoute> },
        { path: 'upcoming', element: <ProtectedRoute><UpComing /></ProtectedRoute> },
        { path: 'profile', element: <ProtectedRoute><Profile userData={userData} /></ProtectedRoute> },
        { path: 'login', element: <Login saveUserData={saveUserData} /> },
        { path: 'register', element: <Register /> }
      ]
    }
  ]);


  return <>
    <div>
      <Offline><div className="offline">You Are Offline , Please check your connection !</div></Offline>
    </div>
    <RouterProvider router={routers}></RouterProvider>

  </>

}

