

import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { Link } from 'react-router-dom';
import MediaItem from '../MediaItem/MediaItem';

export default function SimilarItems({ id, media_type }) {
    const [similarItems, setSimilarItems] = useState([]);
    const [mediaType, setMediaType] = useState('');

    async function fetchSimilarItems() {
        let endpoint = '';
        if (media_type === 'movie') {
            setMediaType('movie');
            endpoint = `https://api.themoviedb.org/3/movie/${id}/similar?api_key=025437452774b9a20481138d2e38affc`;
        } else if (media_type === 'tv') {
            setMediaType('tv');
            endpoint = `https://api.themoviedb.org/3/tv/${id}/similar?api_key=025437452774b9a20481138d2e38affc`;
        }

        try {

            const { data } = await axios.get(endpoint);
            const filteredItems = data.results.filter(item => item.poster_path);  //only data with img
            setSimilarItems(filteredItems);
            // setSimilarItems(data.results);
        } catch (error) {
            console.error('Error fetching similar items:', error);
        }
    }

    useEffect(() => {
        fetchSimilarItems();
    }, [id, media_type]); // Add id and media_type to the dependency array


    return (
        <>
            <div className="row">
                {similarItems.map((item) => (
                    <div className="col-md-2  overflow-hidden mb-4">
                        <Link to={`/itemdetails/${item.media_type || mediaType}/${item.id}`} className='linkStyle link-hover position-relative'>
                            {/* <MediaItem key={item.id} item={item} mediaType={item.media_type || mediaType} /> */}
                            <div className="item position-relative ">
                                {item.poster_path ? <img className='w-100' src={"https://image.tmdb.org/t/p/w500" + item.poster_path} alt="" /> : <img className='w-100' src={"https://image.tmdb.org/t/p/w500" + item.profile_path} alt="" />
                                }

                                {item.vote_average && item.vote_average !== '0' ? <div className="vote position-absolute top-0 end-0  p-2">{item.vote_average.toFixed(1)}</div> : ''}
                            </div>
                            <div className="overlay-item position-absolute  d-flex justify-content-center align-items-center p-3">
                                <h3 className='h6'>{item.title}{item.name}</h3>


                            </div>
                        </Link>
                    </div>

                ))}
            </div>
        </>
    );
}
