import axios from 'axios';
import Joi from 'joi';
import React, { useEffect, useState } from 'react'
import { Helmet } from 'react-helmet';
import { Link, useNavigate } from 'react-router-dom';



export default function Register() {
    let navigate = useNavigate();


    let [user, setUser] = useState({
        first_name: '',
        last_name: '',
        email: '',
        password: '',
        age: 0
    })
    let [error, setError] = useState([]);
    let [validationError, setValidationError] = useState([]);
    let [isLoading, setIsLoading] = useState(false);


    function getUserData(e) {
        // copy useState Data
        let newUser = { ...user };
        newUser[e.target.name] = e.target.value;
        setUser(newUser);
        // console.log(user);
    }

    async function sendDataToAPI() {
        let { data } = await axios.post('https://movies-api.routemisr.com/signup', user);
        console.log(data)
        if (data.message == 'success') {
            setIsLoading(false);
            navigate('/login');

        }
        else {
            setIsLoading(false);
            setError(data.message);


        }




    }
    function submitRegistrationForm(e) {
        setIsLoading(true)
        e.preventDefault();
        let validation = validationRegisterForm()

        if (validation.error) {
            setIsLoading(true)

            setValidationError(validation.error.details);
            // console.log(validationError)
            setIsLoading(false)

        }
        else {
            setIsLoading(true)

            sendDataToAPI();



        }


    }
    function validationRegisterForm() {
        let scheme = Joi.object({
            first_name: Joi.string().min(3).max(30).required(),
            last_name: Joi.string().min(3).max(30).required(),
            email: Joi.string().email({ minDomainSegments: 2, tlds: { allow: ['com', 'net'] } }).required(),
            password: Joi.string().min(10).max(30).required(),
            age: Joi.number().min(16).max(80).required()


        }
        );
        return scheme.validate(user, { abortEarly: false });



    }

    return (
        <>
            <Helmet>
                <title>Register</title>
            </Helmet>

            <form onSubmit={submitRegistrationForm} className='vh-100 pt-5'>
                {/* {validationError.map((err, index) => {
                    if (err.context.label === 'password') {
                        return <div key={index} className="alert alert-danger py-2">Invalid Password</div>


                    }
                    else {
                        return <div key={index} className="alert alert-danger py-2">{err.message} </div>


                    }
                })} */}
                {validationError.map((error, index) => <div key={index} className="alert alert-danger py-2">{error.message} </div>)}

                {error.length > 0 ? <div className="alert alert-danger py-2">{error}</div>
                    : ''}
                <label htmlFor="first_name">First Name</label>
                <input onChange={getUserData} type="text" name='first_name' id='first_name' className='form-control my-2 input-style' />

                <label htmlFor="last_name">Last Name</label>
                <input onChange={getUserData} type="text" name='last_name' id='last_name' className='form-control my-2 input-style' />

                <label htmlFor="email">Email</label>
                <input onChange={getUserData} type="text" name='email' id='email' className='form-control my-2 input-style' />

                <label htmlFor="password">Password</label>
                <input onChange={getUserData} type="password" name='password' id='password' className='form-control my-2 input-style' />

                <label htmlFor="age">Age</label>
                <input onChange={getUserData} type="number" name='age' id='age' className='form-control my-2 input-style' />
                <p className='m-3'>Have an account?
                    <Link className='linkStyle text-info ' to={'/login'}> Login</Link>
                </p>
                <div className='d-flex justify-content-center'>
                    <button className='btn btn-info rounded-2 '>{isLoading == true ? <i className='fa fa-spinner spin'></i>
                        : 'Register'}  </button>
                </div>


            </form>
        </>

    )
}
