import React from 'react'
import { Helmet } from 'react-helmet'

export default function Profile(props) {
    return (
        <>
            <Helmet>
                <title>Profile</title>
            </Helmet>
            <div className='pt-5'>
                Hello,, {props.userData.first_name} {props.userData.last_name}
            </div>
        </>

    )
}
