

// ItemDetails.js
import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import SimilarItems from '../SimilarItems/SimilarItems';
import PersonMovies from '../PersonMovies/PersonMovies';
import { Helmet } from 'react-helmet';
import LoadingScreen from '../../LoadingScreen/LoadingScreen';


export default function ItemDetails() {
    let { id, media_type } = useParams();
    let [itemDetails, setItemDetails] = useState({});
    let [loadingScreen, setLoadingScreen] = useState(true);


    async function fetchItemDetails() {
        setLoadingScreen(true)

        let { data } = await axios.get(`https://api.themoviedb.org/3/${media_type}/${id}?api_key=025437452774b9a20481138d2e38affc`);
        setItemDetails(data);
        setLoadingScreen(false)

    }

    useEffect(() => {
        fetchItemDetails();
    }, [id, media_type]); // Add id and media_type to the dependency array


    return (
        <>
            <Helmet>
                <title>{itemDetails.title || itemDetails.name}</title>
            </Helmet>

            {loadingScreen ? <LoadingScreen /> :
                <>



                    <div className="details-body position-relative  w-100 pt-5" style={{ backgroundImage: `url(https://image.tmdb.org/t/p/w500${itemDetails.poster_path})` }}>
                        {media_type === 'person' ? '' : <div className="details-overlay position-absolute"></div>}




                        <div className="  position-relative  " >
                            <div className="row">
                                <div className="col-md-3">

                                    {itemDetails.poster_path ? <img src={`https://image.tmdb.org/t/p/w500` + itemDetails.poster_path} className="w-100" />
                                        : <img src={`https://image.tmdb.org/t/p/w500` + itemDetails.profile_path} className="w-100" />
                                    }
                                </div>


                                <div className="col-md-8 ">
                                    <h2>{itemDetails.title || itemDetails.name}</h2>
                                    <h3 className='h6'>{itemDetails.overview || itemDetails.biography}</h3>
                                    <div className="genresContainer d-flex">
                                        {itemDetails.genres ? itemDetails.genres.map((genre, index) => <div key={index} className="genre p-2 rounded-2 m-1">{genre.name}</div>) : ''}
                                    </div>
                                    {itemDetails.vote_average !== undefined ? <h4>Vote : {itemDetails.vote_average.toFixed(1)}</h4> : ''}
                                    {/* {itemDetails.vote_average && <h4>Vote : {itemDetails.vote_average.toFixed(1)}</h4>} */}
                                    {itemDetails.vote_count !== undefined ? <h4>Vote Count : {itemDetails.vote_count}</h4> : null}

                                    {/* {itemDetails.vote_count && <h4>Vote Count : {itemDetails.vote_count}</h4>} */}
                                    {itemDetails.release_date ? <h4>Release Date :{itemDetails.release_date} </h4> : null}

                                    {/* {itemDetails.release_date && <h4>Release Date :{itemDetails.release_date} </h4>} */}



                                </div>
                            </div>
                        </div>

                    </div>
                    {media_type === 'person' ? <div className="related-items  left-0 bottom-0 w-100 py-2">
                        <h3 className='related-text'>Related Work</h3>


                    </div> : <div className="related-items  left-0 bottom-0 w-100 py-2">
                        <h3 className='related-text'>More Like This</h3>
                    </div>}

                    <div className='my-4'>

                        {media_type === 'person' ? (
                            <PersonMovies id={id} />
                        ) : (
                            <SimilarItems id={id} media_type={media_type} />
                        )}
                    </div>


                </>
            }
        </>
    );
}
