import axios from 'axios'
import React, { useEffect } from 'react'
import { useState } from 'react';
import MediaItem from '../MediaItem/MediaItem';
import { Link } from 'react-router-dom';
import { Helmet } from 'react-helmet';
import LoadingScreen from '../../LoadingScreen/LoadingScreen';


export default function TvShow() {
    let [allTv, setAllTv] = useState([]);
    let [genres, setGenres] = useState([]);
    let [filteredTv, setFilteredTv] = useState([]);
    let [currentPage, setCurrentPage] = useState(1);
    let [totalPages, setTotalPages] = useState(0);
    let [loadingScreen, setLoadingScreen] = useState(true);



    async function getAllTv() {
        setLoadingScreen(true)

        let { data } = await axios.get(`https://api.themoviedb.org/3/discover/tv?api_key=025437452774b9a20481138d2e38affc&page=${currentPage}`);
        setAllTv(data.results);
        setFilteredTv(data.results);

        // console.log(data.results)
        setTotalPages(data.total_pages)
        setLoadingScreen(false)


    }
    async function getAllGenres() {
        let { data } = await axios.get(`https://api.themoviedb.org/3/genre/tv/list?api_key=025437452774b9a20481138d2e38affc`);
        setGenres(data.genres);
        // console.log(data.genres);
    }

    async function filterByGenre(genre_id) {
        setLoadingScreen(true)

        let { data } = await axios.get(`https://api.themoviedb.org/3/discover/tv?api_key=025437452774b9a20481138d2e38affc&with_genres=${genre_id}&page=${currentPage}`);
        setFilteredTv(data.results);
        console.log(data.results)
        setTotalPages(data.total_pages)
        setLoadingScreen(false)





    }
    let handleCurrentPage = (pageNumber) => {
        setCurrentPage(pageNumber);

    }
    useEffect(() => {
        getAllGenres()
        getAllTv()


    }, []);

    useEffect(() => {
        getAllTv()
    }, [currentPage])
    return (
        <>
            <Helmet>
                <title>TV Shows</title>
            </Helmet>
            {loadingScreen ? <LoadingScreen /> :
                <>


                    <div className="genres position-fixed w-100 py-3">
                        <div className="row d-flex justify-content-center align-items-center">
                            <div className="col-md-2">
                                <h2 className='text-start'>TV Shows</h2>

                            </div>
                            <div className="col-md-8">
                                <li className="nav-item dropdown list-unstyled">
                                    <Link className="nav-link dropdown-toggle" href="#" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                                        Genres
                                    </Link>
                                    <ul className="dropdown-menu">
                                        <li><Link className="dropdown-item " onClick={getAllTv} href="#">All</Link></li>
                                        {genres.map((genre) => (
                                            <li key={genre.id}><Link className="dropdown-item" onClick={() => filterByGenre(genre.id)} >{genre.name}</Link></li>
                                        ))}
                                    </ul>
                                </li>

                            </div>


                        </div>


                    </div>





                    {/* <div className="row d-flex flex-wrap ">
                <button onClick={getAllTv} className='btn btn-outline-primary myBtn  m-1'>All</button>
                {genres.map((genre) => <button onClick={() => filterByGenre(genre.id)} key={genre.id} className='btn btn-outline-primary text-capitalize myBtn m-1'>{genre.name}</button>
                )}


            </div > */}
                    <div className="row genre-items">
                        {filteredTv.map((movie, index) => <MediaItem key={index} item={movie} mediaType={'tv'} />)}
                    </div >

                    <div className="navigate-slides text-center d-flex justify-content-center">
                        {currentPage > 1 && <button className='btn btn-info' onClick={() => handleCurrentPage(currentPage - 1)}><i class="fa-solid fa-arrow-left"></i></button>}
                        <h4 className='px-3'>{currentPage}</h4>
                        {currentPage < totalPages && <button className='btn btn-info' onClick={() => handleCurrentPage(currentPage + 1)}><i class="fa-solid fa-arrow-right"></i></button>}


                    </div>
                </>}
        </>
    )
}

