import axios from 'axios';
import React, { useEffect, useState } from 'react'
import MediaItem from '../MediaItem/MediaItem';
import { Helmet } from 'react-helmet';
import LoadingScreen from '../../LoadingScreen/LoadingScreen';


export default function People() {
    let [people, setPeople] = useState([]);
    let [loadingScreen, setLoadingScreen] = useState(true);


    async function getPeople() {
        setLoadingScreen(true)

        let { data } = await axios.get(`https://api.themoviedb.org/3/person/popular?api_key=025437452774b9a20481138d2e38affc`);
        setPeople(data.results);
        console.log(data.results)
        setLoadingScreen(false)

    }
    useEffect(() => {
        getPeople();
    }, [])
    return (
        <>

            <Helmet>
                <title>People</title>
            </Helmet>
            {loadingScreen ? <LoadingScreen /> :
                <>




                    <div className="row">
                        {people.map((person) => <MediaItem key={person.id} item={person} mediaType={'person'} />)}
                    </div>
                </>

            }
        </>
    )
}
